import weCropper from '../../common/weCropper.js';
const device = uni.getSystemInfoSync();
const width = device.windowWidth;
const height = device.windowHeight;
export default {
	data() {
		return {
			token:'',
			cropperOpt: {
				id: 'cropper',
				width: width,
				height: height,
				scale: 2.5,
				zoom: 8,
				cut: {
					x: (width - 295) / 2,
					y: (height - 295) / 2,
					width: 295,
					height: 295
				}
			},
			weCropper: ''
		};
	},
	methods: {
		back() {
			uni.redirectTo({
				url: '../infoDetail/infoDetail'
			});
		},
		touchStart(e) {
			this.weCropper.touchStart(e);
		},
		touchMove(e) {
			this.weCropper.touchMove(e);
		},
		touchEnd(e) {
			this.weCropper.touchEnd(e);
		},
		convertBase64UrlToBlob(dataURI, type) {
			var binary = atob(dataURI.split(',')[1]);
			var array = [];
			for (var i = 0; i < binary.length; i++) {
				array.push(binary.charCodeAt(i));
			}
			return new Blob([new Uint8Array(array)], { type: type }, { filename: '1111.jpg' });
		},
		blobToDataURL(blob) {
			var a = new FileReader();
			a.readAsDataURL(blob); //读取文件保存在result中
			a.onload = function(e) {
				var getRes = e.target.result; //读取的结果在result中
			};
		},
		getCropperImage() {
			let _this = this;
			//let pathurl = url + '/user/upload';上传服务器地址
			this.weCropper.getCropperImage(avatar => {
				if (avatar) {
					//  获取到裁剪后的图片
					uni.showLoading({
						title:'修改中'
					})
					uni.uploadFile({
						url: this.$baseUrl+'/v1/member/uploadAvatar', 
						filePath: avatar,
						name: 'avatar',
						formData:{
							device:'Mini',
							system_version:'Mini',
							sign:'F7F5F3472016A909FF6A510E1DA92505',
							verid:7,
							token:this.token,
							package:'com.videow.mini',
							uuid:'1',
							appid:'fierteronzd',
							mac_address:'02:00:00:00:00:00',
							agentname:'default',
							timestamp:'1550746155',
							from:3,
							userua:'Mini',
							params:{}
						},
						header: {
							  'content-type':'multipart/form-data'
						  },
						success: (uploadFileRes) => {
							uni.showToast({
								title:'修改成功',
								mask:true
							})
							setTimeout(()=>{
								uni.navigateBack();
							},1000)
						},
						complete:()=>{
							uni.hideLoading();
						}
					});
				} else {
					uni.showToast({
						title:'获取图片失败,请稍后重试'
					})
				}
			});
		},
		uploadTap() {
			const self = this;

			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success(res) {
					let src = res.tempFilePaths[0];
					//  获取裁剪图片资源后，给data添加src属性及其值

					self.weCropper.pushOrign(src);
				}
			});
		}
	},
	onLoad(option) {
		uni.getStorage({
			key: 'TOKEN',
			success: (res)=> {
				this.token = res.data
			}
		});
		// do something
		const cropperOpt = this.cropperOpt;
		const src = option.src;
		if (src) {
			Object.assign(cropperOpt, {
				src:src
			});

			this.weCropper = new weCropper(cropperOpt)
				.on('ready', function(ctx) {
				})
				.on('beforeImageLoad', ctx => {
					uni.showToast({
						title: '上传中',
						icon: 'loading',
						duration: 3000
					});
				})
				.on('imageLoad', ctx => {
					uni.hideToast();
				});
			this.weCropper.pushOrign(src);
		}
	}
};
